import React from "react";
import { useFetch } from '../hooks/useFetch';

const GrandchildComponent = React.lazy(() => import("./GrandchildComponent"));

const ChildComponent: React.FC = () => {
  const { data, error } = useFetch('https://microsoftedge.github.io/Demos/json-dummy-data/5MB.json');

  if (error) return <div>`Error Child component ${error}`</div>
  if (data) console.log('Child data: ', data);

  return (
    <div>
      <div>Child component</div>
      <div>&darr;</div>
      <GrandchildComponent />
    </div>
  );
};

export default ChildComponent;
